
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  color: #657180;
  font-size: 16px;
}
.daily-menu {
  width: 150px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  background: #f5f7f9;
}
.daily-menu-list {
  font-size: 18px;
  text-align: center;
  margin: 5px 0;
  padding: 10px 0;
  cursor: pointer;
  border-right: 2px solid transparent;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
.daily-menu-item p {
  margin-left: 20%;
  font-size: 18px;
  line-height: 40px;
  height: 40px;
  cursor: pointer;
}
.daily-menu-item:hover {
  background: #e3e8ee;
}
.daily-menu-item.on {
  border-right: 2px solid #3399ff;
}
.daily-list {
  width: 300px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 150px;
  overflow: auto;
  border-right: 1px solid #d7dde4;
}
.daily-menu ul {
  list-style: none;
}
.daily-menu ul li a {
  display: block;
  color: inherit;
  text-decoration: none;
  padding: 5px 0;
  margin: 5px 0;
  cursor: pointer;
}
.daily-menu ul li a:hover,
.daily-menu ul li a.on {
  color: #3399ff;
}
.daily-date {
  text-align: center;
  margin: 10px 0;
}

.daily-item {
  display: block;
  color: inherit;
  text-decoration: none;
  padding: 16px;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
.daily-item:hover {
  background: #e3e8ee;
}
.daily-img {
  width: 80px;
  height: 80px;
  float: left;
}
.daily-img img {
  width: 100%;
  height: 100%;
  border-radius: 3px;
}
.daily-title {
  padding: 10px 5px 10px 90px;
}
.daily-title.noImg {
  padding-left: 5px;
}

.daily-article {
  margin-left: 450px;
  padding: 20px;
}
